import { VueComponent, Prop } from 'vue-typescript'

import { setStringModel } from '../../services/string_model'

@VueComponent({
    template: require('./button_toggler.html')
})
export class ButtonToggler extends Vue {

    @Prop valueModel:string = '';
    @Prop options:string[] = [];
    @Prop selectedColor:string = 'btn-primary';
    @Prop notSelectedColor:string = '';
    @Prop size:string = 'xs';
    @Prop defaultValue:string = '';

    value:string = '';

    selected(val:string) {
        this.value = val;
        setStringModel(this.$parent, this.valueModel, this.value);
    }

    ready() {
        if(this.defaultValue) this.value = this.defaultValue;
        else {
            if (this.options.length > 0){
                this.value = this.options[0];
            }
        }
    }
}


