import { VueComponent } from 'vue-typescript'

@VueComponent('ccb-home-view', {
    template: require('./home.html'),
    style: require('./home.sass')
})
export class HomeView extends Vue{
    //Data
    private response_text:string = 'My anwser will appear here!';
    private question_text:string = '';
    private faculty:string = '';
    //Methods
    private ask() {
        if(this.question_text == '' || !this.question_text) return;
        this.$http.get('/cbot/ask?q=' + encodeURI(this.question_text)).then(
            function(res) {
                console.log(res.text());
                this.response_text = res.text()
            },
            function(err){
                console.log(err);
                this.response_text = "Sorry, there was an error processing your question, please try again in a few moments."
            }
        )
    }

    ready(){
        var parts = location.hostname.split('.');
        var subdomain = parts.shift();
        this.faculty = subdomain.charAt(0).toUpperCase() + subdomain.substring(1);
    }
}


