import { VueComponent } from 'vue-typescript'

import { Link } from '../../models/link'

@VueComponent({
    template: require('./questions.html')
})
export class QuestionsView extends Vue {
    sidenavLinks:Link[] = [
        new Link('Questions', '/dashboard/data/questions'),
        new Link('Emergency Numbers', '/dashboard/data/emergency')
    ]

    sidenavActiveStrat(link:Link):boolean{
        if(this.$route.path.indexOf(link.path) == 0) return true;
        else return false;
    }
}


