import { VueComponent } from 'vue-typescript'

import { Link } from '../../models/link'

@VueComponent({
    template: require('./metrics.html')
})
export class MetricsView extends Vue {
    sidenavLinks:Link[] = [
        new Link('Stats', '/dashboard/metrics/stats'),
        new Link('Unanswered', '/dashboard/metrics/unanswered')
    ]

    sidenavActiveStrat(link:Link):boolean{
        if(this.$route.path.indexOf(link.path) == 0) return true;
        else return false;
    }
}


