import { VueComponent, Prop } from 'vue-typescript'

import { Link } from '../../models/link'

@VueComponent({
    template: require('./sidenav.html'),
    style: require('./sidenav.sass')
})
export class Sidenav extends Vue {
    @Prop
    links:Link[] = [
        new Link('Default', '/')
    ]

    @Prop activeStrategy(link:Link):boolean {
        return this.$route.path == link.path;
    }
}


