import { VueComponent, Prop } from 'vue-typescript'

@VueComponent({
    template: require('./toggle_panel.html')
})
export class TogglePanel {
    @Prop headerType:string = 'default';
    @Prop bodyBg:string = '';
    @Prop visible:boolean = false;
}


