import { VueComponent, Prop } from 'vue-typescript'

import { IEvent } from '../../../../../shared/models.ts'
import { TimeService } from '../../../services/time_service.ts'

@VueComponent({
    template: require('./event_datalist.html')
})
export class EventDatalist extends Vue {
    @Prop events:IEvent[] = []

    resizeInfo(){
        var width = $('.list-group-item').width() - 30;
        $('.extra-info').width(width);
    }

    formatEventsDate(date_str:string):string {
        return TimeService.formatEventsDate(date_str);
    }

    formatEventsAliases(aliases:string[]):string{
        if (aliases.length == 0) return '';
        var str:string = aliases[0];
        for (var i = 1; i < aliases.length; i++) {
            str += ', ' + aliases[i];
        }
        return str;
    }
}


