import { VueComponent } from 'vue-typescript'

import { IQuestion } from '../../../../../../shared/models'
import * as Utils from '../../../../services/utils'

@VueComponent({
    template: require('./question-new.html'),
    style: require('./question-new.scss')
})
export class QuestionNewView extends Vue {
    errorMessage = '';
    successMessage = '';

    keywordInput:string = '';    
    questionInput:string = '';    

    question:IQuestion =  {
        name: '',
        keywords: [],
        questions: [],
        response: ''
    }

    sdInfoOpen:boolean = false;

    addQuestion() {
        this.questionInput = this.normaliseString(this.questionInput);
        if (this.question.questions.indexOf(this.questionInput) > -1){
            return;
        }

        this.question.questions.push(this.questionInput);
        this.questionInput = '';
    }

    removeQuestion(q:any){
        this.question.questions.$remove(q);
    }

    addKeyword() {
        var kws = this.keywordInput.toLowerCase().split(' ');

        for (var kw of kws){
            if(this.question.keywords.indexOf(kw) > -1){
                continue
            }

            this.question.keywords.push(kw)
        }

        this.keywordInput = '';
    }

    removeKeyword(keyword:string) {
        if(this.question.keywords.indexOf(keyword) < 0){
            return;
        }

        this.question.keywords.$remove(keyword)
    }

    infoclick() {
        this.sdInfoOpen = true;
        this.$nextTick(function(){
            var offset = $("#sdinfo").offset();
            $("html,body").animate({
                scrollTop: offset.top,
                scrollLeft: offset.left
            });
        })
    }

    saveQuestion(){
        this.successMessage = '';
        if (this.question.name == '') this.errorMessage = 'Missing question name'
        else if (this.question.keywords.length == 0) this.errorMessage = 'Must have at least one keyword'
        else if (this.question.questions.length == 0) this.errorMessage = 'Must have at least one question'
        else if (this.question.response == '') this.errorMessage = 'Missing response'
        else {
            this.question.name = this.normaliseString(this.question.name);
            
            this.$http.post('/cbot/question', this.question).then(
                function(res){
                    this.errorMessage = '';
                    this.successMessage = 'Question created successfully, click <a href="/?#!/dashboard/data/questions/' + res.json().id + '">here</a> to view it'
                
                    this.question =  {
                        name: '',
                        keywords: [],
                        questions: [],
                        response: ''
                    }
                },
                function(err){
                    try {
                        var error = err.json()
                        console.log(error);
                        this.errorMessage = error.message;
                        if (this.errorMessage.indexOf('question') > 0 && error.extra && error.extra._id){
                            console.log('in');
                            this.errorMessage = this.errorMessage.replace('question', '<a href="/?#!/dashboard/data/questions/' + error.extra._id + '">question</a>')
                        }
                    } catch (error) {
                        this.errorMessage = 'there was an error processing your request'
                    }
                }
            )
        }

    }

    normaliseString(str:string):string {
        if (str.charAt(str.length - 1) == '?') {
            str = str.substring(0, str.length - 1);
        }

        return str.charAt(0).toUpperCase() + str.toLowerCase().substring(1);
    }

}



