import { VueComponent, Prop } from 'vue-typescript'

import { IVenue } from '../../../../../shared/models.ts'

@VueComponent({
    template: require('./venue_datalist.html')
})
export class VenueDatalist extends Vue {
    @Prop venues:IVenue[] = []

    resizeInfo(){
        var width = $('.list-group-item').width() - 30;
        $('.extra-info').width(width);
    }

    formatVenuesDate(date:Date):string {
        console.log(date.toLocaleDateString());
        
        var date_str = dayNames[date.getDay()] + ' ' + monthNames[date.getMonth()] + ' ' + date.getDate();
        date_str = date_str + ', ' + getTimeString(date);
        return date_str;
    }

    formatVenuesAliases(aliases:string[]):string{
        if (aliases.length == 0) return '';
        var str:string = aliases[0];
        for (var i = 1; i < aliases.length; i++) {
            str += ', ' + aliases[i];
        }
        return str;
    }
}

function getTimeString(date:Date):string {
    let hour:any = date.getHours();
    let minutes:any = date.getMinutes();
    let halfDay = 'am';
    if (hour > 11) {
        if(hour != 12) hour = hour - 12;
        halfDay = 'pm';
    }
    if (minutes < 10) minutes = '0' + minutes;

    return hour + ':' + minutes + halfDay;
}

const monthNames = [
    'Jan',
    'Feb',
    'Mar',
    "Apr",
    'May',
    'Jun',
    'Jul',
    'Aug',
    'Sep',
    'Oct',
    'Nov',
    'Dec'
]

const dayNames = [
    'Mon',
    'Tue',
    'Wed',
    'Thu',
    'Fri',
    'Sat',
    'Sun'
]



