import { VueComponent, Prop, Watch } from 'vue-typescript'
import { focusModel } from 'vue-focus'

function coerceBoolean(val){
  var rtn = typeof val !== "string" ? val :
  val === "true" ? true :
  val === "false" ? false :
  val === "null" ? false :
  val === "undefined" ? false : val;
  return rtn;
}

@VueComponent({
    template: require('./typeahead.html'),
    style: require('./typeahead.sass'),
    partials: {
      'default': '<span v-html="item | highlight query"></span>',
    },
    filters: {
      highlight(value, phrase) {
        return value.replace(new RegExp('('+phrase+')', 'gi'), '<strong>$1</strong>')
      }
    },
    directives: {
        focusModel
    }
})
export class Typeahead extends Vue{
    created() {
      this.items = this.primitiveData
    }

    @Prop({type: Array}) data:any 
    @Prop({type: Number}) limit:number = 8
    @Prop({type: String}) async:string
    @Prop({type: String}) template:string
    @Prop({type: String}) templateName:string = 'default'
    @Prop({type: String}) key:string
    @Prop({
      type: Boolean,
      coerce: coerceBoolean,
      default: false
    }) matchCase:boolean = false
    @Prop({
      type: Boolean,
      coerce: coerceBoolean,
      default: false
    }) matchStart:boolean = false
    @Prop({type: Function}) onHit:(items:any, other?:any)=>void = function(items) {
        this.reset()
        this.query = items
      }
    @Prop({type: String}) placeholder:string;
    @Prop({type: Object}) extraOption:any = {
      name: null,
      action: function(){
      }
    }
    
    @Prop valueModel:string = ''
    @Prop focusModel:string = ''

    input_focus:boolean = false;

    query:string = ''
    showDropdown:boolean = false
    noResults:boolean = true
    current:number = 0
    items:any = []
    loading:boolean

    @Watch('query')
    queryChanged(){
        this.$parent[this.valueModel] = this.query;
    }

    @Watch('input_focus')
    focusChange(focus){
        this.$parent[this.focusModel] = focus;
    }

    get primitiveData() {
        if (this.data) {
          return this.data.filter(value=> {
            value = this.matchCase ? value : value.toLowerCase();
            var query = this.matchCase ? this.query : this.query.toLowerCase();
            return this.matchStart ? value.indexOf(query) === 0 : value.indexOf(query) !== -1;
          }).slice(0, this.limit)
        }
      }

    ready() {
      // register a partial:
      if (this.templateName && this.templateName!=='default')
      {
        Vue.partial(this.templateName, this.template)
      }
    }

    update() {
      if (!this.query) {
        this.reset()
        return false
      }
      if (this.data) {
        this.items = this.primitiveData
        this.showDropdown = this.items.length || this.extraOption.name ? true : false
      }
      if (this.async) {
        console.error('NO AJAX FUNCTIONALITY')
        // callAjax(this.async + this.query, (data)=> {
        //   this.items = (this.key ? data[this.key] : data).slice(0, this.limit)
        //   this.showDropdown = this.items.length ? true : false
        // })
      }
    }

    reset() {
      this.items = []
      this.query = ''
      this.loading = false
      this.showDropdown = false
    }
    
    setActive(index) {
      this.current = index
    }
    
    isActive(index) {
      return this.current === index
    }
    
    hit(e) {
      e.preventDefault()
      this.onHit(this.items[this.current], this);
    }
    
    up() {
      if (this.current > 0) this.current--
    }
    
    down() {
      if (this.current < this.items.length - 1) this.current++
    }
}


