import { VueComponent } from 'vue-typescript'

@VueComponent('ccb-login-view', {
    template: require('./login.html'),
    style: require('./login.sass')
})
export class LoginView extends Vue{

    email:string = ''
    pw:string = ''

    toDash(){
        var fac = '';
        var parts = location.hostname.split('.');
        if (parts.length < 3) {
        } else {
            var subdomain = parts.shift();
            if (subdomain == 'science' || subdomain == 'management'){
                fac = subdomain;
            } else {
            }
        }

        if (fac == 'management' && this.email == '' && this.pw == 'frankforpresident'){
            this.$router.go('/dashboard');
        }
        if (fac == 'science' && this.email == '' && this.pw == 'frankforprimeminister'){
            this.$router.go('/dashboard');
        }
    }

}


