import { VueComponent } from 'vue-typescript'

import { IQuestion } from '../../../../../../shared/models'
import * as Utils from '../../../../services/utils'

@VueComponent({
    template: require('./question-edit.html'),
    style: require('./question-edit.scss')
})
export class QuestionEditView extends Vue {
    errorMessage = '';
    successMessage = '';

    questionInput:string = '';    
    keywordInput:string = '';    

    question:IQuestion =  {
        name: '',
        keywords: [],
        questions: [],
        response: ''
    }

    sdInfoOpen:boolean = false;

    addQuestion() {
        if (this.questionInput.charAt(this.questionInput.length - 1) == '?') {
            this.questionInput = this.questionInput.substring(0, this.questionInput.length - 1);
        }

        this.questionInput = this.questionInput.charAt(0).toUpperCase() + this.questionInput.toLowerCase().substring(1);

        if (this.question.questions.indexOf(this.questionInput) > -1){
            return;
        }

        this.question.questions.push(this.questionInput);
        this.questionInput = '';
    }

    removeQuestion(q:any){
        this.question.questions.$remove(q);
    }

    addKeyword() {
        var kws = this.keywordInput.toLowerCase().split(' ');

        for (var kw of kws){
            if(this.question.keywords.indexOf(kw) > -1){
                continue
            }

            this.question.keywords.push(kw)
        }

        this.keywordInput = '';
    }

    removeKeyword(keyword:string) {
        if(this.question.keywords.indexOf(keyword) < 0){
            return;
        }

        this.question.keywords.$remove(keyword)
    }

    infoclick() {
        this.sdInfoOpen = true;
        this.$nextTick(function(){
            var offset = $("#sdinfo").offset();
            $("html,body").animate({
                scrollTop: offset.top,
                scrollLeft: offset.left
            });
        })
    }

    saveQuestion(){
        this.successMessage = '';
        if (this.question.name == '') this.errorMessage = 'Missing question name'
        else if (this.question.questions.length == 0) this.errorMessage = 'Must have at least one question'
        else if (this.question.response == '') this.errorMessage = 'Missing response'
        else {
            this.$http.put('/cbot/question/' + this.question._id, this.question).then(
                function(res){
                    this.errorMessage = '';
                    this.successMessage = 'Question saved successfully, click <a href="/?#!/dashboard/questions/' + res.json().id + '">here</a> to view it'
                },
                function(err){
                    try {
                        console.log(err.json())
                    } catch (error) {
                        this.errorMessage = 'there was an error processing your request'
                    }
                }
            )
        }

    }

    ready(){
        this.$http.get('/cbot/question/' + this.$route.params.id).then(
            function(res){
                this.question = res.json();
            },
            function(err){
                this.errorMessage = 'there was an error fetching the question data'
            }
        )
    }

}



