import { VueComponent } from 'vue-typescript'

@VueComponent({
    template: require('./stats.html'),
    style: require('./stats.scss')
})
export class MetricsStatsView extends Vue {
    metrics:any = {}

    ready(){
        this.$http.get('/cbot/metrics').then(
            function(res){
                this.metrics = res.json();
            },
            function(err){
                console.log(err);
            }
        )
    }
}


