import { VueComponent, Prop, Watch } from 'vue-typescript'

@VueComponent({
    template: require('./datepicker.html'),
    style: require('./datepicker.sass')
})
export class Datepicker extends Vue {
    @Prop parentDate:string
    @Prop width:number = 120
    @Prop selected_date:Date = new Date(Date.now())
    @Prop autoclose:boolean = false
    base_date:Date = new Date()
    dates:Date[][] = []
    cal_visible:boolean = false;

    toggleCal(){
        this.base_date = this.selected_date;
        this.cal_visible = !this.cal_visible
    }

    daysInMonth(month, year) {
        month += 1; //1 based
        return new Date(year, month, 0).getDate();
    }

    @Watch('base_date')
    populateDates(){
        var year = this.base_date.getFullYear();
        var month = this.base_date.getMonth();
        var daysInMonth = this.daysInMonth(month, year);
        var first = new Date(year, month, 1);
        var last = new Date(year, month, daysInMonth);
        this.dates = [];
        var week = []
        var counter = 0;
        for (var i = -first.getDay() + 1; i < daysInMonth + (7 - last.getDay()); i++){
            if (counter == 7) {
                this.dates.push(week);
                week = [];
                counter = 0;
            }
            counter++;
            week.push(new Date(year, month, i));
        }
        if (week.length > 0) this.dates.push(week);
    }

    arrowClicked(direction:string){
        if(direction == 'right'){
            this.base_date = new Date(this.base_date.getFullYear(), this.base_date.getMonth() + 1, 1);
        } else if (direction == 'left'){
            this.base_date = new Date(this.base_date.getFullYear(), this.base_date.getMonth() - 1, 1);
        }
    }

    dateClicked(date:Date){
        this.selected_date = date;
        this.$parent[this.parentDate] = date;
        this.base_date = date;
        if(this.autoclose) this.cal_visible = false;
    }

    ready(){
        this.selected_date = new Date(this.selected_date.getFullYear(), this.selected_date.getMonth(), this.selected_date.getDate());
        this.$parent[this.parentDate] = this.selected_date;
        this.base_date = this.selected_date;
    }

    get baseMonth():string {
        return month_names[this.base_date.getMonth()];
    }
    get baseYear():number {
        return this.base_date.getFullYear();
    }

    get input_text():string {
        return month_names[this.selected_date.getMonth()] + ' ' + this.selected_date.getDate() + ', ' + this.selected_date.getFullYear();
    }
}

const month_names = [
    'Jan',
    'Feb',
    'Mar',
    'Apr',
    'May',
    'Jun',
    'Jul',
    'Aug',
    'Sep',
    'Oct',
    'Nov',
    'Dec'
]


