export class TimeService {
    
    static formatEventsDate(date_str:string):string {
        var date = new Date(date_str);
        var date_str = TimeService.dayNames[date.getDay()] + ' ' + TimeService.monthNames[date.getMonth()] + ' ' + date.getDate();
        date_str = date_str + ', ' + TimeService.getTimeString(date);
        return date_str;
    }
    
    static getTimeString(date:Date):string {
        let hour:any = date.getHours();
        let minutes:any = date.getMinutes();
        let halfDay = 'am';
        if (hour > 11) {
            if(hour != 12) hour = hour - 12;
            halfDay = 'pm';
        }
        if (minutes < 10) minutes = '0' + minutes;

        return hour + ':' + minutes + halfDay;
    }

    static monthNames = [
        'Jan',
        'Feb',
        'Mar',
        "Apr",
        'May',
        'Jun',
        'Jul',
        'Aug',
        'Sep',
        'Oct',
        'Nov',
        'Dec'
    ]

    static dayNames = [
        'Mon',
        'Tue',
        'Wed',
        'Thu',
        'Fri',
        'Sat',
        'Sun'
    ]
}


