import { VueComponent, Prop } from 'vue-typescript'

import { Link } from '../../models/link'

@VueComponent({
    template: require('./dashnav.html')
})
export class DashNavbar extends Vue{
    active_path:string = '';

    links:Link[] = [
        new Link('Home', '/dashboard'),
        new Link('Data', '/dashboard/data/questions'),
        new Link('Metrics', '/dashboard/metrics/stats')
    ]

    //there's probably a better way to do this...
    isActiveRoute(path){
        var path_arr:string[] = path.split('/');;
        var route_arr:string[] = this.$route.path.split('/');
        path_arr.push('');
        route_arr.push('');
        if(path_arr[2] == route_arr[2]) return true
        return false
    }
}




