import { VueComponent } from 'vue-typescript'

import { IQuestion } from '../../../../../../shared/models'
import * as Utils from '../../../../services/utils'

@VueComponent({
    template: require('./question-info.html'),
    style: require('./question-info.scss')
})
export class QuestionInfoView extends Vue {
    errorMessage = '';
    successMessage = '';

    deleted = false;

    question:IQuestion =  {
        _id: '',
        name: '',
        keywords: [],
        questions: [],
        response: ''
    }

    deleteQuestion(){
        this.$http.delete('/cbot/question/' + this.question._id).then(
            function(res){
                this.deleted = true;
            },
            function(err){
                this.errorMessage = 'there was an error deleteing the question'
            }
        )
    }

    ready(){
        this.$http.get('/cbot/question/' + this.$route.params.id).then(
            function(res){
                this.question = res.json();
                console.log(res.json())
            },
            function(err){
                this.errorMessage = 'there was an error fetching the question data'
            }
        )
    }

}



