import * as Vue from 'vue'
import * as VueRouter from 'vue-router'
import * as VueResource from 'vue-resource'
var vueScrollTo = require('vue-scrollTo');

Vue.use(VueRouter);
Vue.use(VueResource);
Vue.use(vueScrollTo);

import './style'

import './components'
import './directives'

import { FacultiesView } from './views/faculties/faculties'
import { QuestionState } from './states/question/question'
    import { HomeView } from './views/home/home'
    import { LoginView } from './views/login/login'
import { DashboardState } from './states/dashboard/dashboard'
    import { DashHome } from './views/dashhome/dashhome'
    import { QuestionsView } from './views/questions/questions'
        import { QuestionListView } from './views/questions/subviews/question-list/question-list'
        import { QuestionNewView } from './views/questions/subviews/question-new/question-new'
        import { QuestionInfoView } from './views/questions/subviews/question-info/question-info'
        import { QuestionEditView } from './views/questions/subviews/question-edit/question-edit'
        import { EmergencyNumberListView } from './views/emergency-numbers/emergency-numbers-list/emergency-list'
    import { MetricsView } from './views/metrics/metrics'
        import { MetricsStatsView } from './views/metrics/subviews/stats/stats'
        import { MetricsUnansweredView } from './views/metrics/subviews/unanswered/unanswered'
    import { DataView } from './views/data/data'
        import { DataOverview } from './views/data/subviews/overview/overview'
        import { DataEvents } from './views/data/subviews/events/events'
            import { NewEventView } from './views/data/subviews/event_new/new_event'
            import { EventInfoView } from './views/data/subviews/event_info/event_info'
        import { DataVenues } from './views/data/subviews/venues/venues'
            import { NewVenueView } from './views/data/subviews/venue_new/new_venue'
            import { VenueInfoView } from './views/data/subviews/venue_info/venue_info'
        import { ExactQuestionNewView } from './views/data/subviews/exact_question_new/exact_question_new'
import { NotFoundView } from './views/404/404'

var app = Vue.extend({});

var router = new VueRouter();

router.map({
    '/' : {
        component: function(){
            var parts = location.hostname.split('.');
            if (parts.length < 3) {
                return FacultiesView
            } else {
                var subdomain = parts.shift();
                if (subdomain == 'science' || subdomain == 'management'){
                    return QuestionState
                } else {
                    return FacultiesView
                }
            }
        }(),
        subRoutes: {
            '/': {
                component: HomeView
            },
            'login' : {
                component: LoginView
            }
        }
    },
    '/dashboard': {
        component: DashboardState,
        subRoutes: {
            '/' : {
                component: DashHome
            },
            '/data': {
                component: QuestionsView,
                subRoutes: {
                    '/questions': {
                        component: QuestionListView
                    },
                    '/questions/new': {
                        component: QuestionNewView
                    },
                    '/questions/:id': {
                        component: QuestionInfoView
                    },
                    '/questions/:id/edit': {
                        component: QuestionEditView
                    },
                    '/emergency': {
                        component: EmergencyNumberListView
                    }
                }
            },
            '/metrics': {
                component: MetricsView,
                subRoutes: {
                    '/stats': {
                        component:MetricsStatsView
                    },
                    '/unanswered': {
                        component:MetricsUnansweredView
                    }
                }
            }
        }
    },
    '*': {
        component: NotFoundView
    }
});

router.start(app, '#ccb-main');


