export function setStringModel(object:any, str:string, value:any) {
    var str_parts = str.split('.');
    if (str_parts.length == 1) {
        object[str] = value;
        return;
    } 

    var tmp = object;
    for (var i = 0; i < str_parts.length - 1; i++) {
        tmp = object[str_parts[i]];
    }
    tmp[str_parts[str_parts.length - 1]] = value;
}


