import { VueComponent, Prop } from 'vue-typescript'

@VueComponent({
    template: require('./datalist.html'),
    style: require('./datalists.sass')
})
export class DataList extends Vue {
    @Prop type:string = '';
    @Prop basePath = '/';
    @Prop data:{
            name:string,
            _id:string    
        }[] = [];

    ready(){
        if (this.basePath.charAt(this.basePath.length - 1) != '/') this.basePath += '/'
    }

}




