import { VueComponent } from 'vue-typescript'

import { IQuestion } from '../../../../../../shared/models.ts'

@VueComponent({
    template: require('./question-list.html'),
    style: require('./question-list.scss')
})
export class QuestionListView extends Vue {
    questions:IQuestion[] = [];

    search = ''

    ready(){
        this.questions = [];
        console.log('ready')
        this.$http.get('/cbot/question').then(
            function(res) {
                console.log(res.json())
                this.questions = res.json();
            },
            function(err) {
                console.log(err);
            }
        )
    }
}


