import { VueComponent } from 'vue-typescript'

import { IEmergencyNumbers } from '../../../../../shared/models'

@VueComponent({
    template: require('./emergency-list.html'),
    style: require('./emergency-list.scss')
})
export class EmergencyNumberListView extends Vue {
    numbers:IEmergencyNumbers[] = [];
    newNumber:IEmergencyNumbers = {
        faculty: '',
        name: '',
        number: ''
    }

    search = ""

    errorMessage = ''
    successMessage = ''

    addNumber(){
        this.newNumber = {
            faculty: '',
            name: '',
            number: ''
        }
        this.$broadcast('new-number:show')
    }

    cancelNumber(){
         this.newNumber = {
            faculty: '',
            name: '',
            number: ''
        }
        this.$broadcast('new-number:hide')
    }

    createNumber(){
        this.$http.post('/cbot/emergency', this.newNumber).then(
            function(res) {
                this.successMessage = "Number added successfully, add another one or click close"
                this.newNumber = {
                    faculty: '',
                    name: '',
                    number: ''
                }
                this.reloadNumbers();
            },
            function(err) {
                console.log(err);
                this.errorMessage = "There was an error creating the number"
            }
        )
    }

    removeNumber(number){
        console.log(number)
        this.$http.delete('/cbot/emergency/' + number._id).then(
            function(res){
                this.reloadNumbers();
            },
            function(err){
                console.log(err);
            }
        )
    }

    reloadNumbers(){
        this.$http.get('/cbot/emergency').then(
            function(res) {
                console.log(res.json())
                this.numbers = res.json();
            },
            function(err) {
                console.log(err);
            }
        )
    }

    ready(){
        this.numbers = [];
        this.reloadNumbers();
    }
}


